.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.auth-card {
  background: white;
  border-radius: 15px;
  padding: 40px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  max-width: 450px;
  width: 100%;
}

.auth-header {
  text-align: center;
  margin-bottom: 30px;
}

.auth-header h1 {
  color: #667eea;
  margin-bottom: 10px;
}

.auth-header p {
  color: #666;
}

.auth-tabs {
  display: flex;
  gap: 10px;
  margin-bottom: 25px;
  border-bottom: 2px solid #e1e8ed;
}

.tab {
  flex: 1;
  padding: 12px;
  border: none;
  background: transparent;
  color: #666;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  border-bottom: 3px solid transparent;
  transition: all 0.3s;
}

.tab.active {
  color: #667eea;
  border-bottom-color: #667eea;
}

.auth-form {
  margin-bottom: 20px;
}

.auth-footer {
  text-align: center;
  color: #666;
  font-size: 0.9rem;
}

@media (max-width: 500px) {
  .auth-card {
    padding: 30px 20px;
  }
}
